import React, {Component} from 'react'
import GSAP from 'react-gsap-enhancer'
import Intro from './Intro'

class Home extends Component {

    render() {
        return (
            <div>
                <section className="intro">
                    <Intro />
                </section>

                <div className="ssh">
                    <pre>
                    ssh-rsa 
                    </pre>
                </div>
            </div>
        )
    }
}

export default GSAP()(Home)



